<template>
	<view class="container">
		<!-- 导航 -->
		<nui-navigation-bar @init="initNavigation" @change="opacityChange" :scrollTop="scrollTop" title="用户中心"
			background="var(--base)" />
		<!-- 背景图 -->
		<nui-headbg></nui-headbg>

		<view class="dq-forum">

			<!-- 站点信息 -->
			<nui-headinfo :setSite="setSite" :Sitenum="Sitenum"></nui-headinfo>




			<nui-user-card :userlist="hotuserlist"></nui-user-card>
			<view class="card">
				<nui-list-view title="更多服务">


					
					
					<nui-list-cell :arrow="true">

						<view class="nui-item-box ">
							<nui-icon name="send" :size="24" color="#19be6b"></nui-icon>
							<view class="nui-list-cell_name">微信公众号</view>
							<view class="nui-ml-auto">
								<nui-tag padding="10rpx 12rpx" margin="0 30rpx 0 0" size="24rpx" type="light-green"
									shape="circle">取悦数码</nui-tag>
							</view>

						</view><button open-type="feedback" class="nui-item-box-btn">
						</button>
					</nui-list-cell>

					<!-- #endif -->
				</nui-list-view>
			</view>


		</view>
		<nui-footer :fixed="false" :copyright="copyright" :navigate="navigate" class="nui-custom">
		</nui-footer>
		<!-- 站点信息 -->
		<view class="safearea-bottom"></view>





	</view>
</template>

<script>
	import mpHtml from 'components/mp-html/mp-html';
	import config from '@/utils/config.js';

	export default {

		components: {
			mpHtml
		},
		data() {
			return {
				navigate: [{
					url: "/pages/user/userxy",
					type: "navigate",
					text: "用户协议"
				}, {
					url: "/pages/user/userys",
					type: "navigate",
					text: "隐私协议"
				}],
				copyright: " Copyright © 2022 取悦工作室",


			};
		},
		props: {
			setSite: {
				type: [Object, Array],

			},
			Sitenum: {
				type: [Object, Array],


			},
			sticklist: {
				type: [Object, Array],

			},
			hotuserlist: {
				type: [Object, Array],

			},
			Sitenav: {
				type: [Object, Array],

			},

			scrollTop: {
				type: Number,
				default: 0
			}
		},


		methods: {
			initNavigation(e) {
				this.opacity = e.opacity;

			},
			opacityChange(e) {
				this.opacity = e.opacity;
			},

			clickgg(e) {
				var url = config.Notice;
				console.log(url)
				uni.navigateTo({
					url: url
				});
			},
			clickauthor(e) {
				var url = config.About;

				uni.navigateTo({
					url: url
				});
			},
			clickplan(e) {
				var url = config.Update;

				uni.navigateTo({
					url: url
				});
			}

		},

		onPageScroll(e) {

			this.scrollTop = e.scrollTop;

		},

	};
</script>

<style lang="scss">
	page {
		background: #f7f7fa;
	}

	.nui-item-box {
		width: 100%;
		display: flex;
		align-items: center;
		position: relative;
	}

	.nui-item-box-btn {
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		height: 40px;
		width: 100%;
	}

	.nui-list-cell_name {
		padding-left: 20rpx;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.nui-ml-auto {
		margin-left: auto;
	}

	.nui-right {
		margin-left: auto;
		margin-right: 34rpx;
		font-size: 26rpx;
		color: #999;
	}

	.card {
		background-color: #fff;
		border-radius: 12px;
		padding: 13px;
		margin: 13px;
	}

	nuicustom {
		margin-bottom: 15px;
	}
</style>
